@import '../_includes';


/* HEADER start */

.logo {
  display: flex;
  justify-content: center; }

// Header styles

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.3rem 1rem;
  max-width: 1332px;
  margin: 0 auto;

  &-wrapper {
    background-color: $color-charcoal; }

  &-right {
    display: flex;
    align-items: center; } }

.profile {
  display: flex;
  align-items: center;

  &__name {
    font-weight: 600;
    font-size: 1.125rem;
    color: $color-white; }

  &__img {
    width: 28px;
    height: 28px;
    border-radius: 50%;
    margin-left: 1rem; } }

.search-box {
  width: fit-content;
  height: fit-content;
  position: relative;
  margin-right: 1.5rem; }

.input-wrapper {
  display: none;
  background-color: #fff;
  box-shadow: 0 4px 28px rgba(6, 6, 90, 0.06);
  position: absolute;
  right: 0;
  z-index: 1500;
  border-radius: 5px;
  padding: 3rem 2.4rem; }

.input-search {
  width: 200px;
  background-color: #f8f8f8; }

.btn-search {
  width: 3.125rem;
  height: 3.125rem;
  border-style: none;
  font-size: 1.25rem;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  border-radius: 50%;
  color: #000;
  background-color: transparent;
  pointer-events: painted; }

// Navigation styles

.nav {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  &__list {
    float: left; }

  &__link,
  .nav__link-dropdown {
    display: inline-block;
    color: $color-white;
    text-align: center;
    padding: 1rem 1.3rem;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.125rem; }

  &__list.nav__dropdown {
    display: inline-block;

    .nav__link-dropdown {
      padding: 1rem 2.5rem 1rem 1.3rem;
      position: relative;
      &:after {
        content: "";
        position: absolute;
        right: 1rem;
        top: 50%;
        transform: translateY(-50%) rotate(180deg);
        width: 0;
        height: 0;
        border-left: 4px solid transparent;
        border-right: 4px solid transparent;
        border-bottom: 4px solid $color-white; } } }


  .nav__dropdown--active {
    .nav__link-dropdown {
      &:after {
        transform: translateY(-50%) rotate(0deg);
        border-bottom: 4px solid $color-malachite; } } }


  .nav__dropdown-content {
    display: none;
    position: absolute;
    background-color: $color-white;
    min-width: 160px;
    z-index: 1;
    box-shadow: 0 4px 28px rgba(6, 6, 90, 0.06);
    border-radius: 5px;
    overflow: hidden;
    .nav__link {
      color: $color-dark-charcoal;
      padding: 1rem 1.3rem;
      text-decoration: none;
      display: block;
      text-align: left;
      font-size: 1rem;

      &:after {
        content: none; }

      &:hover {
        background-color: $color-cultured;
        &:after {
          content: none; } } } }

  .nav__dropdown:hover {
    .nav__dropdown-content {
      display: block; } }

  &--header {
    .nav__list {
      .nav__link:hover {
        color: $color-malachite; } } }

  .nav__link-dropdown {
    &--active {
      color: $color-malachite; } }

  &--mobile {
    display: none;
    position: fixed;
    top: 82px;
    left: 0;
    width: 100%;
    background-color: $color-charcoal;
    padding: 0;
    z-index: 1000;
    flex-direction: column;
    align-items: initial;
    .nav__link {
      &:hover {
        background-color: $color-dark-charcoal; } }

    .nav__dropdown-content {
      position: initial;
      display: block;
      background-color: inherit;
      min-width: unset;
      z-index: 1;
      box-shadow: unset;
      border-radius: unset;
      overflow: hidden;
      padding-left: 2rem;

      .nav__link {
        color: $color-white;

        &:hover {
          background-color: $color-dark-charcoal; } } }

    &-active {
      display: flex;

      .nav__list.nav__dropdown {
        .nav__link {
          position: relative;

          &:after {
            content: none; } } } } } }

.nav-icon {
  width: 40px;
  height: 1.5rem;
  position: relative;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
  display: none;

  span {
    display: block;
    position: absolute;
    height: 4px;
    width: 100%;
    background: $color-malachite;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: .25s ease-in-out; } }

.nav-icon span:nth-child(1) {
  top: 0; }

.nav-icon span:nth-child(2),
.nav-icon span:nth-child(3) {
  top: 10px; }

.nav-icon span:nth-child(4) {
  top: 1.25rem; }

.nav-icon.open span:nth-child(1) {
  top: 10px;
  width: 0%;
  left: 50%; }

.nav-icon.open span:nth-child(2) {
  transform: rotate(45deg); }

.nav-icon.open span:nth-child(3) {
  transform: rotate(-45deg); }

.nav-icon.open span:nth-child(4) {
  top: 10px;
  width: 0%;
  left: 50%; }



